html {
  font-size: 62.5%;
}

body {
  background-color: $primaryColor;
}

h1,
h2,
p,
ul,
li,
button {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

img {
  display: block;
  width: 100%;
}

.wrapper {
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
}
